/**
 * App styling
 */


$header-height: 114px;

.nav-header {
  background-color: #4c5c96;
  display: flex;
  align-items: center;
  justify-content: center;

  height: $header-height;

  .search-wrapper {
    width: 50vw;
    display: flex;
    gap: 60px;
  }

  .search-bar {
    width: 50%;
  }
}

.App {
  height: calc(100vh - #{$header-height});
}

@import '~react-pro-sidebar/dist/scss/variables.scss';


// Sidebar Styling from https://github.com/azouaoui-med/react-pro-sidebar
$sidebar-width: 300px !important;
$sidebar-bg-color: #4c5c96 !important;
$submenu-bg-color: rgba(84, 86, 147, 0.05) !important;
$submenu-bg-color-collapsed: rgba(29, 29, 29, 0.95) !important;
$sidebar-color: #ffffff;
$highlight-color: #d8d8d8;
$icon-bg-color: rgba(255, 255, 255, 0.05);
$icon-size: 35px;
$breakpoint-xs: 480px !default;
$breakpoint-sm: 576px !default;
$breakpoint-md: 768px !default;
$breakpoint-lg: 992px !default;
$breakpoint-xl: 1200px !default;


$map-padding: 0px;
$sidebar-toggle-padding: 0.75em;

@import '~react-pro-sidebar/dist/scss/partials/animation';
@import '~react-pro-sidebar/dist/scss/partials/sidebar';
@import '~react-pro-sidebar/dist/scss/partials/menu';
@import '~react-pro-sidebar/dist/scss/partials/popperArrow';
@import '~react-pro-sidebar/dist/scss/partials/reactSlidedown';
// @import '~react-pro-sidebar/dist/scss/styles.scss';

// overwrite css rule, as the scss variable 
// $sidebar-width is not applied well when changed
@media (max-width: 768px) {
  .pro-sidebar.md.toggled {
    position: fixed;
    left: 0px !important;
  }

}

body {
  margin: 0;
  height: 100vh;
  color: #353535;
  font-family: 'Roboto', sans-serif;
}

#root {
  height: 100%;
}

.app {
  height: 100%;
  display: flex;
  position: relative;

  .btn-toggle {
    cursor: pointer;
    width: 35px;
    height: 35px;
    background: $sidebar-bg-color;
    color: #fff;
    text-align: center;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    display: none;
  }

  .sidebar-btn-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;

    .sidebar-btn {
      padding: 1px 15px;
      border-radius: 40px;
      background: rgba(255, 255, 255, 0.05);
      color: #adadad;
      text-decoration: none;
      margin: 0 auto;
      height: 35px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-overflow: ellipsis;
      overflow: hidden;

      span {
        margin-left: 5px;
        font-size: 13px;
      }

      &:hover {
        color: $highlight-color;
      }
    }
  }

  .collapsed {
    .sidebar-btn {
      display: inline-block;
      border-radius: 50%;
      width: 35px;
      height: 35px;
      line-height: 40px;
      padding: 0;
    }
  }

  main {
    padding: $map-padding;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto;

    header {
      margin-bottom: 40px;
      border-bottom: 1px solid #efefef;

      h1 {
        display: flex;
        align-items: center;
        transform: translateX(-20px);
      }
    }

    footer {
      margin-top: auto;
      color: #888;
      text-align: center;

      a {
        text-decoration: none;
        color: #888;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  .social-bagdes {
    margin-top: 10px;

    img {
      margin: 5px;
    }
  }

  .block {
    display: flex;
    margin-bottom: 24px;
    font-size: 14px;
    color: #545454;

    >span {
      margin-left: 10px;
    }
  }

  &.rtl {
    direction: rtl;
    text-align: right;

    header {
      h1 {
        transform: translateX(20px);
      }
    }

    .block {
      direction: rtl;

      >span {
        margin-left: 0;
        margin-right: 10px;
      }
    }

    .sidebar-btn-wrapper {
      .sidebar-btn {
        span {
          margin-left: 0;
          margin-right: 5px;
        }
      }
    }
  }

  @media (max-width: 767.98px) {
    overflow-x: hidden;

    header {
      h1 {
        font-size: 24px;
      }
    }
  }

  @media (max-width: $breakpoint-md) {
    .btn-toggle {
      display: flex;
      position: absolute;
      top: $sidebar-toggle-padding;
      left: $sidebar-toggle-padding;
      z-index: 1;
    }

    &.rtl {
      .btn-toggle {
        margin-left: auto;
      }
    }
  }
}

.toast-container{
  width: 200px;
  position: absolute;
  z-index: 99999;
  
}
.Toastify__toast-body{
  font-size:10pt !important;
  color: #bbbbbb;
  .errorTitle {
    color: #ffffff;
  }
}